.numpad-layout{width:284px;height:278px;display:flex;flex-wrap:wrap;justify-content:space-between}.numpad-layout .numpad-layout-key{width:88px;height:52px;border:2px solid #000;text-align:center;text-decoration:none;background:none;justify-content:space-between;font-family:Roboto;font-style:normal;font-weight:500;font-size:16px;line-height:19px}.numpad-layout .numpad-layout-key:nth-child(3n+1){margin-left:0}.numpad-layout .numpad-layout-key:nth-child(3n){margin-right:0}.numpad-layout .numpad-layout-key[value=стереть]{flex-grow:2;margin-right:0;text-transform:uppercase;margin-right:10px}.numpad-layout .numpad-layout-key:hover,.numpad-layout .numpad-layout-key:active,.numpad-layout .numpad-layout-key:focus{background-color:#000;color:#fff}